<template>
  <div>
    <h1 class="text-center my-3"> </h1>
    <table class="table table-bordered text-center">
      <thead>
        <th> </th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr v-for="(item, index) in users" :key="index">
          <td>{{ item.user_name }}</td>
          <td v-if="+item.is_admin"></td>
          <td v-else></td>
          <td>
            <button 
              class="btn btn-sm btn-success"
              @click="showChangePasswordPopup(item.user_name)"> </button>
            <button 
              class="btn btn-sm btn-danger" 
              @click="deleteUser(item.user_name)"> </button>
          </td>
        </tr>
      </tbody>
    </table>
    <div>
      <button class="btn btn-success" @click="showAddUserPopup"> </button>
      {{ userToChangePassword }}
      {{ newPassword }}
    </div>

    <transition name="fade">
      <div class="popup text-center" v-if="showAddUser">
        <form 
          class="popup__form bg-light p-5 rounded"
          @submit.prevent="addUser"
        >
          <h3> </h3>
          <hr>
          <div class="mb-2">
            <input v-model.lazy="newUserData.name" class="form-control" placeholder="" type="text">
          </div>
          <div class="mb-2">
            <input v-model.lazy="newUserData.password" class="form-control" placeholder="" type="password">
          </div>
          <label class="d-block">
            <input v-model.lazy="newUserData.isAdmin" type="checkbox">
              
          </label>
          <hr>
          <input class="btn btn-success" type="submit" value="">
        </form>
        <div class="popup__overlay" @click="hideAddUserPopup"></div>
      </div>
    </transition>

    <transition name="fade">
      <div class="popup" v-if="showChangePassword">
        <form @submit.prevent="changePassword" class="popup__form bg-light p-5 rounded text-center">
          <h3>     {{ userToChangePassword }}</h3>
          <input 
            placeholder="" 
            type="password" 
            class="form-control mb-3"
            v-model="newPassword">
          <button class="btn btn-success btn-block"> </button>
        </form>
        <div class="popup__overlay" @click="hideChangePasswordPopup"></div>
      </div>
    </transition>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      users: [],
      showAddUser: false,
      showChangePassword: false,
      userToChangePassword: null,
      newPassword: '',
      newUserData: {
        name: '',
        password: '',
        isAdmin: false
      }
    };
  },
  methods: {
    getUsers() {
      let query = JSON.stringify({
        query_type: "get_users"
      });

      axios
        .post("/backend/", `query=${query}`)
        .then(res => {
          if (typeof res.data !== "object") 
            throw new Error(res.data);
          this.users = res.data;
        })
        .catch(err => {
          this.users = [];
          console.error(err);
        });
    },

    addUser() {
      if (!this.newUserData.name || !this.newUserData.password) {
        alert('    !');
        return;
      }

      let query = {
        query_type: 'add_user',
        payload: {
          name: this.newUserData.name,
          password: this.newUserData.password,
          isAdmin: this.newUserData.isAdmin,
        }
      }

      axios.post('/backend/', `query=${JSON.stringify(query)}`)
        .then(res => {
          if (res.data) {
            this.getUsers();
            this.hideAddUserPopup();
            this.newUserData.name = '';
            this.newUserData.password = '';
            this.newUserData.isAdmin = false;
          } else {
            alert('   ');
          }
        })
    },

    deleteUser(user_name) {
      if (!confirm('     ' + user_name + '?'))
        return;

      let query = {
        query_type: 'delete_user',
        payload: {
          name: user_name
        }
      }

      axios.post('/backend/', `query=${JSON.stringify(query)}`)
        .then(res => {
          this.getUsers();
        })
    },

    changePassword() {
      if (!this.userToChangePassword || !this.newPassword) {
        alert('  ');
        return;
      }

      let query = {
        query_type: 'change_password',
        payload: {
          name: this.userToChangePassword,
          password: this.newPassword
        }
      }

      axios.post('/backend/', `query=${JSON.stringify(query)}`)
        .then(res => {
          if (res.data) {
            alert('  ');
            this.hideChangePasswordPopup();
          } else {
            alert('   ');
          }
        })
    },

    showAddUserPopup() {
      this.showAddUser = true;
    },

    hideAddUserPopup(e) {
      this.showAddUser = false;
    },

    showChangePasswordPopup(user_name) {
      this.userToChangePassword = user_name;
      this.showChangePassword = true;
    },

    hideChangePasswordPopup() {
      this.showChangePassword = false;
      this.userToChangePassword = null;
      this.newPassword = '';
    }
  },
  created() {
    this.getUsers();
  }
};
</script>

<style lang="scss">
.popup {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;

  &__form {
    cursor: initial;
    z-index: 2;
  }

  &__overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7);
    cursor: pointer;
    transition: all 0.2s;
    z-index: 1;

    &:hover {
      background-color: rgba(0, 0, 0, 0.8);
    }
  }
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .2s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active   2.1.8 */ {
  opacity: 0;
}
</style>



